/* header styles */
#header{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10%;
    padding-top: 10px;
    padding-bottom: 10px;
    height: 100px;
    background-color: green;
}
#header ul{
    list-style-type: none;
    padding:5px;  
    display: flex;
    flex-direction: row;
}
#header li button{
    outline: none;
    background-color:mediumaquamarine;
    border-radius: 10px 30px;
    text-decoration: none;
    font-size: larger;
    font-weight: 700;
    padding: 10px 20px;
    margin: 0 20px;
    box-shadow: 3px 3px 5px black;
    text-shadow: 3px 3px 10px black;
}
/* end of header styles */

/* body styles */

    /* loginForm styles */
#loginForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}
#loginForm input{
    display: inline-block;
    padding: 10px;
    margin: 20px;
    width: 300px;

    font-size: larger;
}
#loginForm button{
    border-radius: 20%;
    padding: 10px 20px;
    margin: 20px;
    background-color: green;
    outline: none;

    color: white;
    font-size: larger;
    font-weight: 700;
}
#loginForm input::placeholder{
    text-align: center;
}
#loginForm > *{
    /* box-shadow: 3px 5px 10px black; */
    text-shadow: 5px 10px 10px black;
}
#loginForm > input, #loginForm > button{
    box-shadow: 3px 5px 10px black;
}
    /* end of loginForm Styles */

    /* signUpForm styles */

#signUpForm{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#signUpForm > input{
    display: inline-block;
    padding: 10px;
    margin: 20px;
    width: 300px;

    font-size: larger;
}

#signUpForm input::placeholder{
    text-align: center;
}
#signUpForm div{
    width: 300px;
    padding: 10px 10px 0 10px;
    margin: 20px 20px 0 0px;
    font-size: larger;
    font-weight: 400;
}
#signUpForm div input{
    box-shadow: unset;
    margin-right: 30px;
}
#signUpForm div label{
    font-size: larger;
    font-weight: 500;
}
#signUpForm input, #signUpForm button{
    box-shadow: 3px 5px 10px black;
    text-shadow: 5px 10px 10px black;
    /* box-shadow: 2px 2px 3px green;
    text-shadow: 2px 2px 15px #777; */
}
#signUpForm h2, #signUpForm label{
    text-shadow: 5px 10px 10px black;
}
#signUpForm button{
    border-radius: 20%;
    padding: 10px 20px;
    margin-top: 50px;
    background-color: green;
    outline: none;

    color: white;
    font-size: larger;
    font-weight: 700;
}
    /* end of signUpForm Styles */

    /* planTrip styles */
#planTripForm p{
    font-size: xx-large;
    text-align: center;
    font-weight: 600;
    text-shadow: 3px 10px 20px black;  
}
#planTrip{
    display: flex;
    justify-content: center;
}
#planTripTable{
    /* margin-top: 50px; */
    margin: 100px 10px;
    width: 80%;
    text-align: left;
    border-collapse: collapse;
}
#planTripTable th{
    padding: 10px;
    margin:20px;
    text-shadow: 2px 2px 5px lightcoral;
    color: maroon;
    font-size: x-large;
}
#planTripTable td > *{
    display: inline-block;
    width: 60%;
    padding: 10px;
    font-size: larger;
    font-weight: 600;
}
#planTripTable td button{
    outline: none;
    width: unset;
    padding: 10px 20px;
    border-radius: 20%;
    background-color: green;
    color: white;
    text-shadow: 3px 3px 5px black;
}
/* #planTripTable *{
    text-shadow: 5px 5px 5px rgb(0, 100, 0);
} */
#planTripTable td *{
    box-shadow: 5px 10px 20px black;
}
#planTripForm{
    margin-bottom: 200px;
}
#planTripForm > button{
    outline: none;
    margin-left: 10%;
    padding: 10px 20px;
    border-radius: 20%;

    font-size: larger;
    font-weight: 600;
    color: white;
    background-color: brown;
    
    box-shadow: 3px 3px 5px black;
    text-shadow: 3px 3px 10px black;   
}
#planTripForm section{
    font-size: x-large;
    color: red;
    text-align: center;
    font-weight: 600;
    text-shadow: 5px 10px 10px #777;    
}
    /* end of planTrip styles */

    /* selectBus styles */


/* #selectBusContainer{
    border: 2px solid red;
} */
#selectBusContainer p{
    font-size: xx-large;
    text-align: center;
    font-weight: 600;
    text-shadow: 3px 10px 20px black;  
}
#selectBus{

    width: 85%;
    border-radius: 15px;    
    margin: 10px auto;    
    border: 5px outset burlywood;
    box-shadow: 10px 10px 10px black;
    text-shadow: 5px 5px 10px #666;
}
#selectBusTable{
    
    width: 100%;
    text-align: left;
    border-collapse: collapse;

}
#selectBusTable tr:nth-child(even){
    background-color: #ccc;
}
#selectBusTable th{
    border-bottom: 3px solid grey;
    padding: 20px;
    font-size: larger;
    font-weight: 700;
}
#selectBusTable td{
    padding: 15px 20px;
    font-size: larger;
    font-weight: 500;
}
#selectBusTable td:last-child{
    padding: unset;
}
#selectBusTable button{
    outline: none;
    padding: 8px 20px;
    border-radius: 20px;
    background-color: green;
    color: white;
    font-size: large;
    font-weight: 700;
    box-shadow: 10px 10px 10px black;
    text-shadow: 3px 3px 5px black;
}
    /* end of selectBus styles */

    /* selectSeats styles */
        /* bus styles */
#selectSeatsContainer{
    display: flex;
    flex-wrap: wrap;
    /* border: 2px solid red;  */
}
#busContainer{
    flex:1;

}
#bus{
    border: 3px solid blue ;
    border-left: 70px solid blue;
    border-radius: 60px 10px 10px 60px;
    width: 750px;
    height: 300px;
    /* margin: 30px 30px 30px auto; */
    margin: 30px auto;
}
#bus button{
    display: inline-flex;
    border-radius: 10px;;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: green;
    color: white;
    width: 65px;
    height: 60px;
    margin: 5px;
    /* border: 2px solid red; */
    outline: none;
    box-shadow: 0px 10px 10px black;
    text-shadow: 0px 10px 5px black;
}
#summaryContainer{
    flex: 1;
}
    /* end of bus styles */

    /* summary styles */
#summary{
    margin:30px 50px;
    /* width: 80%; */
    min-width: 700px;
}
#summary h2{
    font-size: x-large;
    padding: 10px;
    border-bottom: 2px solid black;
}
#summary section span:first-child{
    display: inline-block;
    font-size: larger;
    font-weight: 500;
    width: 50%;
    margin-bottom: 10px;
}
#summary section span:last-child{

    font-size: larger;
    font-weight: 700;
}
#summary form > input {

    display: inline-block;
    width: 30%;
    margin-right: 20%;
    height: 30px;
    font-size: larger;
    font-weight: 500;
    /* text-align: center; */
    border-width: 2px;
    box-shadow:  0px 2px 5px black;
}
#summary form label{

    font-size: larger;
    font-weight: 700;
    margin-right: 25px;

}
#summary form label input{
    display: inline-block;
    margin-right: 10px;
}
#summary button{
    background-color: green;
    color: white;
    font-size: larger;
    padding: 10px 15px;
    border-radius: 20px;
    outline: none;
    box-shadow: 0px 10px 10px black;
    text-shadow: 0px 10px 5px black;
}
#errorBox{
    all: unset;
    font-size: x-large;
    text-align: center;
    color: crimson;
    font-weight: 500;
    text-shadow: 3px 10px 20px black; 
}
    /* end of summary styles */

    /* end of selectSeats styles */
    
    /* paymentForm styles */
#paymentFormContainer{
    width: 80%;
    margin: 50px auto;
    padding: 0 auto;
}
#paymentFormContainer > div{
    background-color: green;
    color: white;
    display: flex;
    padding: 20px 20px;
}
#paymentFormContainer > div > span:first-child{
    display: inline-block;
    font-size: larger;
    font-weight: 700;
}
#paymentFormContainer > div > span:last-child{
    display: inline-block;
    font-size: larger;
    font-weight: 700;
    margin-left: auto;
}
#paymentFormContainer *{
    text-shadow: 5px 10px 10px black;
}
#paymentFormContainer, #paymentFormContainer section > * {
    box-shadow: 10px 10px 30px black;
}
#paymentForm section span{
    display: inline-block;
    font-size: larger;
    font-weight: 600;
    padding: 10px 20px;
    width: 30%;
    text-shadow: 2px 3px 5px black;
    color: orange;
}
#paymentForm section label{
    margin: 20px;
    font-size: larger;
    font-weight: 400;
    text-shadow: 2px 3px 15px black;
    color: orange;
}
#paymentForm section > input{

    font-size: larger;
    font-weight: 500;
    padding: 5px 10px;
    margin: 20px;
    outline: none;
}
#paymentForm section:nth-child(2) input{
    width: 300px;
    
}
#paymentForm section:nth-child(3) input{
    width: 250px;
}
#paymentForm section:nth-child(4) input{
    width: 100px;
}
#paymentForm section:nth-child(5) input{
    width: 150px;
}
#paymentForm section:last-child{
    margin: 20px auto;
    width: 500px;
    display: flex;
    justify-content: space-around;

}
#paymentForm section button:nth-child(1){
    padding: 10px 20px;
    margin:10px;
    background-color: green;
    font-size: larger;
    font-weight: 700;
    color: white;
    border-radius: 10px;
    outline: none;
}
#paymentForm section button:nth-child(2){
    padding: 10px 20px;
    margin:10px;
    background-color: tomato;
    font-size: larger;
    font-weight: 700;
    color: white;
    border-radius: 10px;
    outline: none;
}
    /* end of paymentForm styles */

    /* viewTickets styles */
#viewTickets div{
    background: rgba(0, 128, 0, 0.5);
}
#viewTickets div *{
    text-shadow: 2px 10px 20px #333;
    
}
#viewTickets div{
    margin: 100px 10%;
    padding: 20px;
    border: 3px dashed black;
    box-shadow: 10px 25px 25px #555;
}
#viewTickets div table{
    width: 100%;
    /* border: 2px solid red; */

}
#viewTickets div table th{
    text-align: left;
    /* border: 2px solid red; */
    padding: 10px 5px;
    font-size: larger;
    font-weight: 650;
}
#viewTickets div table td{
    /* border: 2px solid red; */
    padding: 5px 5px;
    font-size: larger;
    font-weight: 500;
}
#viewTickets div table:nth-child(1){
    width: 50%
}
#viewTickets p{
    text-align: right;
    font-style: oblique;
    font-size: medium;
}

    /* end of viewTickets styles */

/* end of body styles */

/* footer styles */
#footer{
    margin-top: 200px;
}
#footer hr{
    color: grey;
    margin:5px 5%;
}
/* end of footer styles */
